*{
    margin:0px;
    padding: 0px;
    box-sizing: border-box;
  }
  h2{
    text-align: center;
    color:white;
    margin: 10px;
  }
  .main-container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 500px;
  }
  @media screen and (max-width:460px){
    .main-container{
      display: flex;
      justify-content: center;
      margin-top:10px;
    }
    .container{
     background-color: darkslategrey;
     border-radius: 10px;
     box-shadow: 2px 5px 10px gray;
     width:100%;
     height:400px;
    }
  }
  .container{
    background-color: darkslategrey;
    border-radius: 10px;
    box-shadow: 2px 5px 10px gray;
    width:300px;
    height:400px;
  
  }
  input{
    border-radius: 10px;
    box-shadow: inset 2px 5px 6px gray;
    border:0px;
    width:270px;
    padding: 10px;
    margin:15px;
  }
  button{
    border-radius: 10px;
    margin-top:10px;
    margin-left:94px;
    border:0px;
    width:110px;
    padding:10px;
    background-color: white;
  }
  button:hover{
    background-color: green;
    transition: 1s;
    color:white;
  }
  .result-btn{
    background-color: aliceblue;
    box-shadow: inset 2px 5px 10px gray;
    border-radius: 10px;
    width:270px;
    padding:20px;
    margin-top:50px;
    margin-left:15px;
  
  }